
.App {
  font-family: 'Arial', sans-serif;
  text-align: center;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

input {
  padding: 8px;
  margin-right: 10px;
}

select {
  padding: 8px;
  margin-right: 10px;
}

.pagination {
  list-style: none;
  display: flex;
  margin: 20px 0;
  padding: 0;
}

.pagination li {
  margin-right: 5px;
}

.pagination a {
  text-decoration: none;
  padding: 8px;
  border: 1px solid #ddd;
  background-color: #f2f2f2;
  color: #333;
}

.pagination a.active {
  background-color: #4CAF50;
  color: white;
}
